"use client";
import React from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip, Legend } from 'recharts';

const COLORS = ['#6366f1', '#10b981', '#f59e42', '#f43f5e', '#818cf8'];
const MOCK_ALLOC = [
  { label: 'Layer 1s', value: 1138000 },
  { label: 'DeFi', value: 811420 },
  { label: 'Stablecoins', value: 648135 },
  { label: 'Tokenized Assets', value: 486652 },
  { label: 'NFTs', value: 192277 },
];

export default function AllocationBreakdown() {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
      <h2 className="text-lg font-semibold text-gray-800 mb-2">Allocation Breakdown</h2>
      <div className="flex flex-col md:flex-row gap-6 items-center">
        <div className="w-40 h-40">
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <Pie
                data={MOCK_ALLOC}
                dataKey="value"
                nameKey="label"
                cx="50%"
                cy="50%"
                outerRadius={70}
                innerRadius={40}
                label={({ name, percent }) => `${(percent * 100).toFixed(0)}%`}
              >
                {MOCK_ALLOC.map((entry, idx) => (
                  <Cell key={`cell-${idx}`} fill={COLORS[idx % COLORS.length]} />
                ))}
              </Pie>
              <Tooltip formatter={v => `$${v.toLocaleString()}`} />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        </div>
        <div className="flex-1">
          <ul className="space-y-2">
            {MOCK_ALLOC.map((a, idx) => (
              <li key={a.label} className="flex justify-between items-center">
                <span className="text-gray-700 flex items-center gap-2">
                  <span className="inline-block w-3 h-3 rounded-full" style={{ backgroundColor: COLORS[idx % COLORS.length] }} />
                  {a.label}
                </span>
                <span className="text-gray-500 text-xs">${a.value.toLocaleString()}</span>
                <span className="text-gray-700 font-semibold">{((a.value / MOCK_ALLOC.reduce((acc, cur) => acc + cur.value, 0)) * 100).toFixed(1)}%</span>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}
